<template>
  <div id="notes">
      <div id="notes-content">
        <h4>Notes</h4>
        <hr>
        <table v-click-outside="clearHighlight">
            <tr v-for="(note,index) in notes" v-dragging="{ item: note, list: notes, group: 'note' }" :key="index" @click="rowClicked(index)">
                <td class="note-no" @mousedown="rowMousedown(index)">
                        {{index+1}}) &nbsp;
                </td>

                <td><textarea class="note-text"  v-model="note.text" @change="resizeTextarea($event)" ></textarea></td>

                <td class='delete-note' v-on:click="confirmBox=true">
                    <span class="icon sunset-bin2"></span>
                </td>
            </tr>
        </table>

        <div class="add-new-note" v-on:click="addNewNote">+ Add Note</div>

      </div>
      <div class="footnote">

           This quotation is subject to the INVITE Communications Terms &amp; Conditions and any notes listed above. Please sign or stamp with your company seal on the affixed request form, make a copy for your records, and then return the original by regular mail or by FAX.

      </div>

      <confirmation-modal v-if="confirmBox" v-on:returnAnswer="keepOrDelete($event)"></confirmation-modal>
  </div>
</template>

<script>
import vClickOutside from 'v-click-outside'
import ConfirmationModal from './ConfirmationModal.vue'

export default {
  name: 'notes',
  props: ['notes'],
  components:{
      'confirmation-modal': ConfirmationModal
  },
  data () {
    return {
        currentIndex: undefined,
        confirmBox:false,
    }
  },
  directives: {
      clickOutside: vClickOutside.directive
  },
  methods: {
      resizeTextarea(event){
        //   console.log(event.srcElement.scrollHeight);

        // We need to first set the height to 0 beacuse scrollHeight will be equal to height(when it is specified)
          event.target.style.height = "0px";
          let height = event.srcElement.scrollHeight;
          event.target.style.height = String(height) + "px";
      },
      addNewNote(){
          this.currentIndex = this.notes.length;
          this.notes.push({
                "item":this.notes.length+1,
                'text': '',
          });
          this.clearHighlight();
      },
      rowClicked(index){
            this.currentIndex = index;
            this.clearHighlight();
            this.highlightRow(index);
      },
      rowMousedown(index){
        this.currentIndex = index;
        this.clearHighlight();
      },
      clearHighlight(){
            let tr = document.querySelectorAll("#notes table tr");
            for(let j = 0; j < tr.length; j++){
            // console.log(j,tr[j].className);
            tr[j].className = tr[j].className.replace(/\s*highlight\s*/ig,' ');
            }
      },
      highlightRow(index){
        // console.log(tr[index+1].className);
        let tr = document.querySelectorAll("#notes table tr");
        tr[index].className += ' highlight';
      },
      keepOrDelete(answer){
          this.confirmBox = false;
          if(answer===true){
                this.notes.splice(this.currentIndex,1);
                this.clearHighlight();
          }
      },
      resizeAllTextarea(){
            let allTextarea = document.querySelectorAll("#notes table textarea");
            allTextarea.forEach(element => {
            // We need to first set the height to 0 beacuse scrollHeight will be equal to height(when it is specified)
            element.style.height = '0px';
            let scroll_height = element.scrollHeight;
            // console.log(scroll_height);
            element.style.height = String(scroll_height) + 'px';
            });
      }
  },
  mounted () {
      //   update textarea height after dragging
    this.$dragging.$on('dragend', () => {
        this.resizeAllTextarea();
        this.clearHighlight();
    });
  },
  updated(){
    //   update textarea height after delting an item
      this.resizeAllTextarea();

        //   highlight the new row after update
      if(this.currentIndex === this.notes.length-1){
          this.highlightRow(this.currentIndex);

          //focus last row textarea
          var lastInput= document.querySelectorAll("#notes table textarea");
          lastInput[lastInput.length-1].focus();
      }
  }
}
</script>

<style lang="scss">
#notes{
    margin: auto;
    margin-top: 80px;
    width: 60%;
    #notes-content{
        background-color: #e6eaef;
        padding: 24px;
        padding-top: 10px;

        h4{
            font-size: 20px;
            text-align: center;
            margin-bottom: 14px;
            margin-top: 0;
        }
        hr{
            // width: 50%;
            // margin:auto;
            margin-bottom: 10px;
        }
        table{
            width: 90%;
            margin:0;
            td{
                border:none;
            }
            td.note-no{
                text-align: right;
                cursor: move;
            }


            td.delete-note{
                color: rgb(138, 85, 85);
                &:hover{
                    cursor: pointer;
                    color: rgb(184, 35, 35);
                    transform: scale(1.3);
                    transition: transform 0.1s ease-in-out;
                }
            }

        }

    }
    .footnote{
        margin-top: 20px;
        font-size: 12px;
        line-height: 16px;
    }

}
div.add-new-note{
    width:100px;
    margin:auto;
    margin-top: 20px;
    border-radius: 6px;

    background: #2c3e50;
    color: white;
    padding: 6px;
    text-align: center;
    cursor: pointer;
    &:hover{
        color: #a3e0c1;
        transition: 0.1s all ease-in;
    }

}

</style>



