<template>
  <div id="monthly-items">
      <div>
        <table>
            <thead>
                <tr>
                    <th class="vanish-cell"></th>
                    <th>No.</th>
                    <th>Monthly Item</th>
                    <th>Qty</th>
                    <th>Unit</th>
                    <th>Price</th>
                    <th>Amount</th>
                    <th>Note</th>
                    <th class="vanish-cell"></th>
                </tr>
            </thead>
            <tbody v-click-outside="clearHighlight">
                <!-- item2 has been used to aviod collision with iinitial items dragging group which is item-->
            <tr v-for="(item2,index) in monthlyItems" v-if="item2.desc!= 'Consumption Tax'" v-dragging="{ item: item2, list: monthlyItems, group: 'item2' }" :key="index" @click="rowClicked(index)" @mousedown="rowMousedown(index)">

                <td class="move-btn" title="Move" @mousedown="rowMousedown(index)">
                    <span class="icon sunset-command"></span>
                </td>

                <td class="input-no">
                    {{index+1}}
                </td>

                <td><textarea class="input-name"  v-model="item2.desc" @change="resizeTextarea($event)"></textarea></td>

                <td><input class="input-quantity" type="number" v-model.lazy="item2.qty" @change="calculate(index)"></td>

                <td><input class="input-unit" type="text" v-model="item2.unit" ></td>

                <td class="input-price"><input type="number" v-model.lazy="item2.price" @change="calculate(index)"></td>

                <td class="input-amount">{{item2.amount}}</td>

                <td><textarea class="input-note"  v-model="item2.comment" @change="resizeTextarea($event)"></textarea></td>

                <td class='delete-button' v-on:click="confirmBox=true">

                    <span class="icon sunset-bin2"></span>

                </td>
            </tr>
            <!-- Consumtion Tax -->
            <tr @click="rowClicked(monthlyItems.length-1)">
                <td class="vanish-cell" ></td>

                <td class="input-no">
                    {{monthlyItems.length}}
                </td>

                <td colspan="3" style="text-align:left; padding:6px;">Consumption Tax</td>

                <!-- <td>{{monthlyItems[monthlyItems.length-1].qty}}</td> -->

                <td><input class="input-unit" type="text" v-model.lazy="monthlyItems[monthlyItems.length-1].unit" @change="calculate(monthlyItems.length-1)"></td>

                <!-- <td>{{monthlyItems[monthlyItems.length-1].price}}</td> -->

                <td colspan="1" class="input-amount">{{monthlyItems[monthlyItems.length-1].amount}}</td>
            </tr>

            <tr class="final-row">
                <td class="vanish-cell"></td>
                <td title="Add New Item" class="add-new-item"><div v-on:click="addNewItem">+</div></td>
                <td colspan="4" ><b>Monthly Cost</b></td>
                <td>{{monthlyCost}}</td>
            </tr>
            </tbody>

        </table>

    </div>

    <confirmation-modal v-if="confirmBox" v-on:returnAnswer="keepOrDelete($event)"></confirmation-modal>
  </div>
</template>

<script>
import vClickOutside from 'v-click-outside'
import ConfirmationModal from './ConfirmationModal.vue'

export default {
  name: 'monthly-items',
  props: ['monthlyItems'],
  components:{
      'confirmation-modal': ConfirmationModal
  },
  data () {
    return {
      currentIndex:undefined,
      monthlyCost: 0,
      confirmBox:false,
    }
  },
  directives: {
      clickOutside: vClickOutside.directive
  },
  methods:{
      rowClicked(index){
            this.currentIndex = index;
            this.clearHighlight()
            this.highlightRow(index);

            if(this.monthlyItems[index].desc === "Consumption Tax"){
                this.currentIndex = undefined;
            }
      },
      rowMousedown(index){
        this.currentIndex = index;
        this.clearHighlight();
      },
      clearHighlight(){
            let tr = document.querySelectorAll("#monthly-items table tr");
            for(let j = 1; j < tr.length; j++){
            // console.log(j,tr[j].className);
            tr[j].className = tr[j].className.replace(/\s*highlight\s*/ig,' ');
            }
      },
      highlightRow(index){
        let tr = document.querySelectorAll("#monthly-items table tr");
        // console.log(index,tr[index+1].className);
        tr[index+1].className += ' highlight';
      },
      calculate: function(index){
          let amount = this.monthlyItems[index].qty * this.monthlyItems[index].price;
          if(! Number.isInteger(amount)){
              amount = Number.parseFloat(amount).toFixed(2);
          }
          this.monthlyItems[index].amount = amount;
          this.findMonthlyCost();

      },
      findMonthlyCost: function(){
          let sum = 0;
          let taxPercentage = 0;
          this.monthlyItems.forEach(function(item){
              if(item.desc == 'Consumption Tax'){
                  taxPercentage = parseFloat(item.unit);
                  console.log(taxPercentage);
              }else{
                  sum+=item.amount;
              }

          });
          if(! Number.isInteger(sum)){
              sum = Number.parseFloat(sum).toFixed(2);
          }
          let tax= parseInt(sum*taxPercentage/100);
        //   console.log(tax);
          this.monthlyItems[this.monthlyItems.length-1].price = tax;
          this.monthlyItems[this.monthlyItems.length-1].amount = tax;
          this.monthlyCost = sum+tax;
          this.$emit('monthlyCostChanged',this.monthlyCost);

      },
      addNewItem(){
          this.currentIndex = this.monthlyItems.length;
          this.monthlyItems.splice(this.monthlyItems.length-1,0,{
                "item":this.monthlyItems.length,
                "desc":"",
                "qty":1,
                "unit":"",
                "price":0,
                "amount":0,
                "comment":""
          });
          this.monthlyItems[this.monthlyItems.length-1].item = this.monthlyItems.length;
          this.clearHighlight();
        //   console.log(this.monthlyItems);
      },
      keepOrDelete(answer){
          this.confirmBox = false;
        //   console.log(answer)
          if(answer===true){
              this.monthlyItems.splice(this.currentIndex,1);
              this.findMonthlyCost();
              this.clearHighlight();
          }
      },
      resizeTextarea(event){
        //   console.log(event.srcElement.scrollHeight);

        // We need to first set the height to 0 beacuse scrollHeight will be equal to height(when it is specified)
          event.target.style.height = "0px";
          let height = event.srcElement.scrollHeight;
          event.target.style.height = String(height) + "px";
      },
      resizeAllTextarea(){
            let allTextarea = document.querySelectorAll("#monthly-items table textarea");
            allTextarea.forEach(element => {
            // We need to first set the height to 0 beacuse scrollHeight will be equal to height(when it is specified)
            element.style.height = '0px';
            let scroll_height = element.scrollHeight;
            // console.log(scroll_height);
            element.style.height = String(scroll_height) + 'px';
            });
      }

  },
  created(){
      this.findMonthlyCost();
  },
  mounted () {
    //   update textarea height after dragging
    this.$dragging.$on('dragend', () => {
        this.resizeAllTextarea();
        this.clearHighlight();
    });

  },
  updated(){
    //   update textarea height after delting an item
      this.resizeAllTextarea();
      if(this.currentIndex === this.monthlyItems.length-1){
          this.highlightRow(this.currentIndex-1);

          //focus last row name textarea
          var lastInput= document.querySelectorAll("#monthly-items table textarea");
          lastInput[lastInput.length-2].focus();
      }
  }
}
</script>

<style lang="scss">
#monthly-items{
    margin-top: 100px;

}
</style>



