<template>
  <div id="confirmation-modal">
    <div class="confirmation-box" v-click-outside="onClickOutside">
        <div>
            Are you sure you want to delete this item ?
        </div>
        <div class="confirmation-buttons">
            <div @click="returnAnswer(true)">Yes</div>
            <div @click="returnAnswer(false)">No</div>
        </div>
    </div>

  </div>
</template>

<script>
import vClickOutside from 'v-click-outside'
export default {
  name: 'confiramtion-modal',
  data () {
    return {

    }
  },
  directives: {
      clickOutside: vClickOutside.directive
  },
  methods:{
    returnAnswer(answer){
        this.$emit('returnAnswer',answer);
    },
    onClickOutside () {
        this.$emit('returnAnswer',false);
    },
  },
}
</script>

<style lang="scss">
@keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
#confirmation-modal{
    position:fixed;
    width:100%;
    height:100%;
    z-index:10;
    top:0;
    left:0;
    background: #2c3e508a;
    opacity:0;
    // transition: opacity 5s ease-out;
    animation: fadeIn 0.5s forwards 1 ease-out;
    .confirmation-box{
        position:absolute;
        top:40%;
        left:36%;
        // width:300px;
        // height:100px;
        background: #2c3e50;
        color:white;
        padding: 40px;
        border-radius: 6px;

        div.confirmation-buttons{
            margin-top: 36px;
            display: flex;
            justify-content: space-around;
            div:nth-child(1){
                background: #a80616;
                padding: 6px 10px;
                border-radius: 6px;
                cursor: pointer;
                &:hover{
                    background: #c4081b;
                    transition: 0.2s all ease-out;
                }
            }
            div:nth-child(2){
                background: rgb(7, 138, 7);
                padding: 6px 10px;
                border-radius: 6px;
                cursor: pointer;
                &:hover{
                    background: rgb(9, 175, 9);
                    transition: all 0.2s ease-out;
                }
            }
        }
    }
}
</style>



