<template>
  <div id="installation-items">
      <div>
        <table>
            <thead>
                <tr>
                    <th class="vanish-cell"></th>
                    <th>No.</th>
                    <th>Installation Item</th>
                    <th>Qty</th>
                    <th>Unit</th>
                    <th>Price</th>
                    <th>Amount</th>
                    <th>Note</th>
                    <th class="vanish-cell"></th>
                </tr>
            </thead>
            <tbody v-click-outside="clearHighlight">

            <tr v-for="(item,index) in initialItems" v-if="item.desc!= 'Consumption Tax'" v-dragging="{ item: item, list: initialItems, group: 'item' }" :key="index" @click="rowClicked(index)" v-on:mousedown="rowMousedown(index)">

                <td class="move-btn" title="Move" v-on:mousedown="rowMousedown(index)">
                    <span class="icon sunset-command"></span>
                </td>

                <td class="input-no">
                    {{index+1}}
                </td>

                <td><textarea class="input-name"  v-model="item.desc" @change="resizeTextarea($event)"></textarea></td>

                <td><input class="input-quantity" type="number" v-model.lazy="item.qty" @change="calculate(index)"></td>

                <td><input class="input-unit" type="text" v-model="item.unit" ></td>

                <td class="input-price"><input type="number" v-model.lazy="item.price" @change="calculate(index)"></td>

                <td class="input-amount">{{item.amount}}</td>

                <td><textarea class="input-note"  v-model="item.comment" @change="resizeTextarea($event)"></textarea></td>

                <td class='delete-button' v-on:click="confirmBox=true">

                    <span class="icon sunset-bin2"></span>

                </td>
            </tr>
            <!-- Consumtion Tax -->
            <tr @click="rowClicked(initialItems.length-1)">
                <td class="vanish-cell" ></td>

                <td class="input-no">
                    {{initialItems.length}}
                </td>

                <td colspan="3" style="text-align:left; padding:6px;">Consumption Tax</td>

                <!-- <td>{{initialItems[initialItems.length-1].qty}}</td> -->

                <td><input class="input-unit" type="text" v-model.lazy="initialItems[initialItems.length-1].unit" @change="calculate(initialItems.length-1)"></td>

                <!-- <td>{{initialItems[initialItems.length-1].price}}</td> -->

                <td  class="input-amount">{{initialItems[initialItems.length-1].amount}}</td>
            </tr>

            <tr class="final-row">
                <td class="vanish-cell"></td>
                <td title="Add New Item" class="add-new-item"><div v-on:click="addNewItem">+</div></td>
                <td colspan="4" ><b>Initial Cost</b></td>
                <td>{{initialCost}}</td>
            </tr>
            </tbody>

        </table>

    </div>

    <confirmation-modal v-if="confirmBox" v-on:returnAnswer="keepOrDelete($event)"></confirmation-modal>
  </div>
</template>

<script>

import vClickOutside from 'v-click-outside'
import ConfirmationModal from './ConfirmationModal.vue'

export default {
  name: 'intallation-items',
  props: ['initialItems'],
  components:{
      'confirmation-modal': ConfirmationModal
  },
  data () {
    return {
      currentIndex:undefined,
      initialCost: 0,
      confirmBox:false,
    }
  },
  directives: {
      clickOutside: vClickOutside.directive
  },
  methods:{
      rowClicked(index){
            this.currentIndex = index;
            this.clearHighlight();
            this.highlightRow(index);

            // To avoid highlighting last row
            if(this.initialItems[index].desc === "Consumption Tax"){
                this.currentIndex = undefined;
            }
      },
      rowMousedown(index){
        this.currentIndex = index;
        this.clearHighlight();
      },
      clearHighlight(){
            let tr = document.querySelectorAll("#installation-items table tr");
            for(let j = 1; j < tr.length; j++){
            // console.log(j,tr[j].className);
            tr[j].className = tr[j].className.replace(/\s*highlight\s*/ig,' ');
            }
      },
      highlightRow(index){
        let tr = document.querySelectorAll("#installation-items table tr");
        // console.log(tr,index);
        // index+1 for the heading
        tr[index+1].className += ' highlight';
      },
      calculate: function(index){

          let amount = this.initialItems[index].qty * this.initialItems[index].price;
          if(! Number.isInteger(amount)){
              amount = Number.parseFloat(amount).toFixed(2);
          }
          this.initialItems[index].amount = amount;
          this.findInitialCost();

      },
      findInitialCost: function(){
          let sum = 0;
          let taxPercentage = 0;
          this.initialItems.forEach(function(item){
              if(item.desc == 'Consumption Tax'){
                  taxPercentage = parseFloat(item.unit);
                  console.log(taxPercentage);
              }else{
                  sum+=item.amount;
              }

          });
          if(! Number.isInteger(sum)){
              sum = Number.parseFloat(sum).toFixed(2);
          }
          let tax= parseInt(sum*taxPercentage/100);
        //   console.log(tax);
          this.initialItems[this.initialItems.length-1].price = tax;
          this.initialItems[this.initialItems.length-1].amount = tax;
          this.initialCost = sum+tax;
          this.$emit('initialCostChanged',this.initialCost);
        //   console.log(this.initialCost);
      },
      addNewItem(){
        //   updating the currentIndex, so that it can be used to highlight new item after the app is updated
          this.currentIndex = this.initialItems.length;
          this.initialItems.splice(this.initialItems.length-1,0,{
                "item":this.initialItems.length,
                "desc":"",
                "qty":1,
                "unit":"",
                "price":0,
                "amount":0,
                "comment":""
          });
          //updating consumption rate item no
          this.initialItems[this.initialItems.length-1].item = this.initialItems.length;

          this.clearHighlight();
      },
      keepOrDelete(answer){
          this.confirmBox = false;
        //   console.log(answer)
          if(answer===true){
              this.initialItems.splice(this.currentIndex,1);
              this.findInitialCost();
              this.clearHighlight();
          }

      },
      resizeTextarea(event){
        //   console.log(event.srcElement.scrollHeight);

        // We need to first set the height to 0 beacuse scrollHeight will be equal to height(when it is specified)
          event.target.style.height = "0px";
          let height = event.srcElement.scrollHeight;
          event.target.style.height = String(height) + "px";
      },
      resizeAllTextarea(){
            let allTextarea = document.querySelectorAll("#installation-items table textarea");
            allTextarea.forEach(element => {
            // We need to first set the height to 0 beacuse scrollHeight will be equal to height(when it is specified)
            element.style.height = '0px';
            let scroll_height = element.scrollHeight;
            // console.log(scroll_height);
            element.style.height = String(scroll_height) + 'px';
            });
      }

  },
  created(){
      this.findInitialCost();
  },
  mounted () {
      //   update textarea height after dragging
    this.$dragging.$on('dragend', () => {
        this.resizeAllTextarea();
        this.clearHighlight();
    })
  },
  updated(){
    //   update textarea height after delting an item
      this.resizeAllTextarea();

    //   highlight the new row after update
      if(this.currentIndex === this.initialItems.length-1){
          this.highlightRow(this.currentIndex-1);

          //focus last row name textarea
          var lastInput= document.querySelectorAll("#installation-items table textarea");
          lastInput[lastInput.length-2].focus();
      }
  }
}
</script>

<style lang="scss">
#installation-items{
    margin-top: 100px;

}
table{
    width: 80%;
    margin: auto;
    border-collapse: collapse;
    tr:nth-child(odd){
        background: #ebeef1;
    }
    tr{
        th{
            padding: 10px;
            background: #c6cdd6;
            border: 1px solid #2c3e50;
        }
        td{
            padding: 0px;
            text-align: center;
            border: 1px solid #2c3e50;
            align-items: center;
        }

        td.move-btn{
            border:2px solid white;
            border-right-color: #2c3e50;
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;
            background: #2c3e50;
            color: white;
            font-size: 20px;
            text-align: center;
            padding: 10px;
            cursor: move;
            &:hover{
                color: rgb(109, 181, 240);
                transition: 0.1s all ease-in;
            }
        }

        th.vanish-cell,td.vanish-cell{
            background:white;
            border: none;
        }

        td input{
            box-sizing: border-box;
            border: none;
            font-size: 14px;
            font-family: 'Lato', sans-serif;
            outline: none;
            width:100%;
            height: 100%;
            padding: 10px;
            color: #2c3e50;
            background: transparent;
        }

        td.input-no{
            border: 1px solid #2c3e50;
        }
        .input-quantity,.input-unit{
            width: 80px;
            text-align: center;
            padding-left: 6px;
            padding-right: 6px;
        }
        td.input-price{
            width: 140px;
            // text-align: right;
            padding: 0;
            input{
                max-width: 120px;
                padding: 0;
                text-align: right;
            }
        }


        td.input-amount{
            text-align: right;
            padding: 6px;
            max-width: 140px;

        }
        td.delete-button{
            border:2px solid white;
            border-left-color: #2c3e50;
            border-top-right-radius: 10px;
            border-bottom-right-radius: 10px;
            background: #2c3e50;
            color: whitesmoke;
            text-align: center;
            padding: 10px;
            cursor: pointer;

            &:hover{
                color: rgb(226, 91, 91);
                transition: 0.1s all ease-in;
            }
        }

    }
    tr:hover{
        background: #a3bee0;
        td{
            input{
                background: transparent;
            }
        }

    }
    tr:focus{
        background: green;
        td{
            input{
                background: green;
            }
        }

    }

    tr.final-row{
        background:#c6cdd6;

        &:hover{
            background:#c6cdd6;
        }
        td:nth-child(3){
            text-align: center;
            font-size: 20px;
            font-weight:bold;
            border: none;
        }
        td:nth-child(4){
            text-align: right;
            padding: 10px;
            font-weight:bold;
            border:none;
            border-left: 1px solid #2c3e50;
        }
    }
    td.add-new-item{
        border:none;
        background: white;
        div{
            border-bottom-left-radius: 10px;
            font-size: 40px;
            font-weight: 900;
            background: #2c3e50;
            color: white;
            padding: 10px 10px;
            cursor: cell;
            &:hover{
                color: #a3e0c1;
                transition: 0.1s all ease-in;
            }
        }
    }
textarea{
    resize: none;
	width: 100%;
	padding: 6px;
	overflow: hidden;
	box-sizing: border-box;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    color: #2c3e50;
    background: transparent;
    border:none;
    outline: none;
    vertical-align: bottom;

}


}


</style>



