<template>
  <div id="app">

    <div id="header-content">
        <input class="company-name" type="text" v-model="headerInfo.company">

        <div>
          Service Type: <input class="service-type" type="text" v-model="headerInfo.type">
          <br>
          Delivery Date: <input class="delivery-date" type="text" v-model="headerInfo.delivery">
          <br>
          Payment Terms: <input class="payment-terms" type="text" v-model="headerInfo.terms">
          <br>
          Expiry Date: <input class="expiration-date" type="text" v-model="headerInfo.expiry">

        </div>
        <br><br>
        <div>
          Initial Cost: <b>{{initialCost}}</b>
          <br>
          Monthly Cost: <b>{{monthlyCost}}</b>

      </div>

    </div><!-- #header-content -->

    <installation-items v-on:initialCostChanged="updateInitialCost($event)" v-bind:initialItems="initialItems"></installation-items>

    <monthly-items v-on:monthlyCostChanged="updateMonthlyCost($event)" v-bind:monthlyItems="monthlyItems"></monthly-items>

    <notes v-bind:notes="notes"></notes>

    <div id="json-button" v-on:click="getJSONData">Get JSON Data</div>

  </div>
</template>

<script>
import InstallationItems from './InstallationItems.vue';
import MonthlyItems from './MonthlyItems.vue';
import Notes from './Notes.vue';
export default {
  name: 'app',
  components:{
    'installation-items': InstallationItems,
    'monthly-items': MonthlyItems,
    'notes': Notes
  },
  data () {
    return {
      initialCost : 0,
      monthlyCost : 0,

      headerInfo : {"record":"6587767327150","company":"Schimmel, Kohler and Ratke","type":"id voluptas","delivery":"2018/11/01","terms":"40 days ut numquam","expiry":"2018/11/21"},

      initialItems: [{"record":"5950608239614","item":1,"desc":"pariatur ex provident autem asperiores nostrum non perferendis et","qty":35,"unit":"JJ","price":7700,"amount":269500,"comment":"Quo excepturi accusamus."},{"record":"0475087231935","item":2,"desc":"sapiente placeat molestiae ex sunt nam dolorum","qty":3,"unit":"DO","price":13000,"amount":39000,"comment":""},{"record":"0701629657902","item":3,"desc":"nisi vel numquam saepe pariatur deserunt voluptate quo qui quae repudiandae atque ipsum reiciendis ipsam maiores ea sunt unde","qty":30,"unit":"VT","price":900,"amount":27000,"comment":""},{"record":"7937680136534","item":4,"desc":"sed temporibus deleniti hic recusandae est et eum odit","qty":2,"unit":"LX","price":13000,"amount":26000,"comment":""},{"record":"6301945070559","item":5,"desc":"omnis eos ut molestiae minus nemo et et nam eligendi voluptatem vero facilis tempora sunt voluptas fugit","qty":5,"unit":"UJ","price":33000,"amount":165000,"comment":""},{"record":"4190074872187","item":6,"desc":"repellendus consectetur perferendis molestiae provident voluptate omnis non voluptas voluptatum blanditiis fugit illum cum atque quisquam consequatur","qty":8,"unit":"DW","price":11000,"amount":88000,"comment":"Voluptas ut."},{"record":"0645645256391","item":7,"desc":"ea veritatis aut aliquid voluptas reiciendis et aliquid sed optio quae unde ipsum","qty":1,"unit":"YD","price":13000,"amount":13000,"comment":"Ducimus fugiat."},{"record":"","item":8,"desc":"Consumption Tax","qty":1,"unit":"8%","price":50200,"amount":50200,"comment":""}],

      monthlyItems: [{"record":"7702243122312","item":1,"desc":"at aliquam sed nihil voluptatem","qty":3,"unit":"QK","price":3800,"amount":11400,"comment":""},{"record":"7280665767545","item":2,"desc":"quia molestiae voluptates saepe expedita rerum vero","qty":10,"unit":"AA","price":1100,"amount":11000,"comment":"Culpa et."},{"record":"7816456114757","item":3,"desc":"atque et fuga dolores et rem esse maiores tempore ut aut incidunt consequuntur minus vero consectetur eaque non totam","qty":21,"unit":"TR","price":45000,"amount":945000,"comment":"Molestiae."},{"record":"5085758344694","item":4,"desc":"eligendi reprehenderit omnis qui qui et et","qty":10,"unit":"TV","price":42000,"amount":420000,"comment":""},{"record":"","item":5,"desc":"Consumption Tax","qty":1,"unit":"8%","price":110992,"amount":110992,"comment":""}],

      notes: [{"record":"6424218011452","item":1,"text":"Odit omnis facere necessitatibus illum rerum aut. Veritatis ipsum aut dolore occaecati omnis. Eum eveniet distinctio voluptas."},{"record":"5191599409949","item":2,"text":"Ullam exercitationem fugiat facere hic est quia dolorem. Aut dolore commodi rerum sed ratione aut vel. Voluptas incidunt maxime architecto et. Deleniti consectetur quidem in."},{"record":"1477284860122","item":3,"text":"Quo sunt neque illum expedita aspernatur odit voluptates. Libero et atque inventore non et quia."},{"record":"7833122014012","item":4,"text":"Cum ut quo unde unde. Ipsa error et cumque commodi et."},{"record":"9449918269009","item":5,"text":"Ipsam iste incidunt in nostrum culpa. Porro magnam blanditiis autem voluptas. Quo placeat et est incidunt. Id expedita a est aut dolor ducimus aut mollitia."}],
    }
  },
  methods:{
    updateInitialCost: function(newInitialCost){
      this.initialCost = newInitialCost;
    },
    updateMonthlyCost: function(newMonthlyCost){
      this.monthlyCost = newMonthlyCost;
    },
    updateIndices(){
      // update serial no of initial items
      for(let i=0; i<this.initialItems.length; i++){
        this.initialItems[i].item = i+1;
      }
      // update serial no of monthly items
      for(let i=0; i<this.monthlyItems.length; i++){
        this.monthlyItems[i].item = i+1;
      }
      // update serial no of notes
      for(let i=0; i<this.notes.length; i++){
        this.notes[i].item = i+1;
      }
    },
    getJSONData(){
      this.updateIndices();
      let initialItemsWithoutTax = this.initialItems.slice(0,this.initialItems.length-1);

      let monthlyItemsWithoutTax = this.monthlyItems.slice(0,this.monthlyItems.length-1);

      console.log('Header Info\n',JSON.stringify(this.headerInfo));
      console.log('\nInstallation Items\n',JSON.stringify(initialItemsWithoutTax));
      console.log('\nMonthly Items\n',JSON.stringify(monthlyItemsWithoutTax));
      console.log('\nNotes\n',JSON.stringify(this.notes));
    },
  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  line-height: 26px;
  // -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  color: #2c3e50;
  margin-top: 20px;
  margin-bottom: 60px;

}
header{
  text-align: center;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  img{
    width: 80px;
    height: 80px;
    margin-right:20px;
  }
  h1{
    font-size: 40px;
  }

}

div#header-content{
  margin:auto;

  // padding-left: 10%;
  width: 80%;
  // max-width: 600px;
  input{
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    color:#2c3e50;
    border: none;
    // border-bottom: 2px solid #2c3e50;
    padding: 6px;
    max-width: 500px;
  }
  input.company-name{
    border:none;
    border-bottom: 1px solid #2c3e50;
    font-size: 22px;
    font-weight: bold;
    padding: 10px;
    width: 80%;
    padding-left:0;
    margin-bottom: 6px;
  }
  input.service-type{
    width: 60%;
     max-width: 400px;
  }
  input.payment-terms{
    width: 60%;
     max-width: 400px;
  }
}
div#json-button{
  margin:60px auto;
  padding: 10px;
  background: #2c3e50;
  color: white;
  border-radius: 10px;
  text-transform: uppercase;
  text-align: center;
  max-width: 200px;
  cursor: pointer;
  &:hover{
    font-size: 18px;
    color:gold;
    transition: 0.1s all ease-out;
  }
}
</style>



